* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-color: #ffffff;
}
img {
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  user-select: none;
  max-width: 100%;
  max-height: 100%;
  width: auto;
  height: auto;
  transition: 0.5s;
}